html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 3rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.site-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7), rgba(57, 70, 89, 0.7)), url(../img/header.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 5rem 0;
}
.site-header .logo {
  max-width: 20rem;
}

.contenido-header {
  padding: 10rem 0;
}
@media (min-width: 768px) {
  .contenido-header {
    max-width: 35%;
  }
}
.contenido-header h1 {
  font-size: 4.5rem;
  color: #FFFFFF;
  font-weight: 900;
  max-width: 100%;
}

@media (min-width: 768px) {
  .crea-grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.crea-contenido {
  color: #313c4e;
}
.crea-contenido h2 {
  margin: 3rem 0;
  font-size: 3.6rem;
  display: inline-block;
  font-weight: 900;
}
@media (min-width: 768px) {
  .crea-contenido h2 {
    margin: 0 0 3rem 0;
  }
}
.crea-contenido h2::after {
  content: "";
  display: block;
  height: 1rem;
  margin: -1.4rem 0 0;
  width: calc(100% + 1rem);
  background-color: #84d9bc;
}

.beneficios {
  background-color: #84d9bc;
}

@media (min-width: 768px) {
  .beneficios-grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .beneficios-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
.beneficios-grid .imagen {
  grid-column: 4/6;
  padding: 2rem 0;
  transition-property: transform;
  transition-duration: 0.3s;
}
.beneficios-grid .imagen:hover {
  transform: rotate(4deg) scale(1.1);
}

.contenido {
  grid-column: 1/4;
}
.contenido h2 {
  color: #313c4e;
  display: inline-block;
  font-weight: 900;
}
.contenido h2::after {
  content: "";
  display: block;
  height: 1rem;
  margin: -1.4rem 0 0;
  width: calc(100% + 1rem);
  background-color: #FFFFFF;
}

.estadisticas-grid h2 {
  color: #313c4e;
  font-size: 3.6rem;
  font-weight: 900;
  display: inline-block;
  margin-bottom: 2rem;
}
.estadisticas-grid h2:after {
  content: "";
  height: 1rem;
  background-color: #84d9bc;
  display: block;
  width: calc(100% + 1rem);
  margin: -1.4rem 0 0;
}
.estadisticas-grid p {
  margin: 2rem 0;
}

.planes {
  background-color: #313c4e;
}
.planes h2 {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 3.6rem;
  display: inline-block;
  margin: 5rem 0 3rem 0;
}
.planes h2::after {
  content: "";
  height: 1rem;
  background-color: #84d9bc;
  display: block;
  margin: -1.4rem 0 0;
  width: calc(100% + 1rem);
}
.planes .descripcion {
  color: #FFFFFF;
}

.planes-grid {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .planes-grid {
    margin-top: 10rem;
    flex-direction: row;
  }
}
.planes-grid .negocio {
  background-image: url(../img/icono_negocio.svg);
  background-color: #84d9bc;
  order: -1;
}
@media (min-width: 768px) {
  .planes-grid .negocio {
    margin: -5rem 0;
    border-radius: 2rem;
    order: unset;
    background-position: 50% 7rem;
    padding-top: 21rem;
    z-index: 1;
  }
}
.planes-grid .negocio .boton {
  border: 2px solid #FFFFFF;
}
.planes-grid .inicio {
  background-image: url(../img/icono_inicio.svg);
}
.planes-grid .pro {
  background-image: url(../img/icono_pro.svg);
}

.plan {
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16rem 4rem 4rem 4rem;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .plan {
    transition-property: transform;
    transition-duration: 0.3s;
  }
  .plan:hover {
    transform: scale(1.1);
  }
}
.plan h3 {
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.plan p {
  margin: 0;
}
.plan ul {
  list-style: none;
}
.plan li {
  margin-bottom: 2rem;
}
.plan .precio {
  font-weight: 900;
}

@media (min-width: 768px) {
  .testimoniales-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
  .testimoniales-grid img {
    grid-column: 1/3;
    margin-inline: auto;
    margin-block: 0;
  }
}

.testimoniales-contenido {
  grid-column: 3/6;
}
.testimoniales-contenido h2 {
  color: #313c4e;
  font-weight: 900;
  display: inline-block;
  margin-bottom: 2rem;
}
.testimoniales-contenido h2::after {
  content: "";
  display: block;
  height: 1rem;
  background-color: #84d9bc;
  width: calc(100% + 1rem);
  margin: -1.4rem 0 0;
}

.testimonio {
  border: 0.5rem solid #313c4e;
  border-radius: 1rem;
  background-color: #84d9bc;
  margin-bottom: 4rem;
  padding: 2rem;
  position: relative;
}
@media (min-width: 768px) {
  .testimonio {
    max-width: 70%;
  }
  .testimonio:last-of-type {
    margin-block: 0;
  }
  .testimonio:nth-child(2) {
    margin-left: 10%;
  }
  .testimonio:nth-child(3) {
    margin-left: 20%;
  }
  .testimonio:nth-child(4) {
    margin-left: 30%;
  }
}
.testimonio::before {
  content: "";
  display: block;
  height: 5rem;
  width: 5rem;
  position: absolute;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center;
}
.testimonio p {
  font-size: 2rem;
  padding: 0 2rem 0 6rem;
  margin: 0;
}

.footer {
  background-color: #313c4e;
}

.footer-grid {
  padding-block: 5rem;
  gap: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .footer-grid {
    display: flex;
    flex-direction: row;
  }
}
.footer-grid .imagen {
  margin-inline: auto;
  flex: 1;
  width: 20rem;
  margin-bottom: 3rem;
}

.enlaces {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.enlaces h3 {
  text-transform: uppercase;
  font-size: 2.8rem;
  color: #84d9bc;
  margin-bottom: 2rem;
}
.enlaces a {
  width: auto;
  color: #FFFFFF;
  line-height: 2;
  font-size: 1.6rem;
}

.copyright {
  text-transform: uppercase;
  border-top: 2px solid #84d9bc;
  margin: 0;
  padding: 0.5rem;
  text-align: center;
  font-size: 1.5rem;
  color: #FFFFFF;
}

.boton {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.boton:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}